<template>
  <div v-if="unsubscribe_status == 'unSubscribedReason'">
    <b-container>
      <b-row>
        <b-col md="7" class="mx-auto">
          <div class="d-flex align-items-center py-4">
            <b-img :src="unsubscribe_form.logo_url" alt="logo" height="40" v-if="unsubscribe_form.logo_url" />
            <h2 class="mb-0 ml-2 brand-heading">{{ unsubscribe_form.company_name }}</h2>
          </div>
          <b-card body-class="p-45">
            <h1 class="mb-2">You have been successfully unsubscribed</h1>
            <div class="mb-3 font-size-lg">
              <p class="mb-0">You will no longer receive emails from us</p>
            </div>
            <div class="mt-4" v-if="isUnsubscribed">
              <h3 class="mb-3 pb-1">Please let us know why you unsubscribed.</h3>
              <b-form class="radio-group-mb-4" @submit.prevent="save">
                <b-form-textarea type="text" v-if="!(options.length >= 1)" class="mb-4 mt-3" required placeholder="Enter Unsubscribe reason" v-model="form.reason" />
                <b-form-radio-group stacked v-model="form.reason" :options="options" required />
                <b-form-textarea type="text" class="mb-4 mt-3" v-if="form.reason == 'Other'" required placeholder="Enter Unsubscribe reason" v-model="unsubReason" />
                <div class="d-flex justify-content-between align-items-center">
                  <div v-if="unsubscribe_form.redirect_url">
                    <b-link :href="unsubscribe_form.redirect_url" target="_blank">Home</b-link>
                  </div>
                  <b-button variant="primary" type="submit" class="px-4 ml-auto"> Submit <b-spinner v-if="clicked" variant="white" small label="Spinning"></b-spinner></b-button>
                </div>
              </b-form>
              <div>
                <p class="float-right mt-4 text-danger" v-if="show_message">{{ message }}</p>
              </div>
            </div>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
  <div v-else-if="unsubscribe_status == 'unSubscribed'">
    <b-container>
      <b-row>
        <b-col md="7" class="mx-auto mt-5">
          <div class="d-flex align-items-center py-4">
            <b-img :src="unsubscribe_form.logo_url" alt="logo" height="40" v-if="unsubscribe_form.logo_url" />
            <h2 class="mb-0 ml-2 brand-heading">{{ unsubscribe_form.company_name }}</h2>
          </div>
          <b-card body-class="p-45">
            <h1 class="mb-2">Already you have been unsubscribed</h1>
            <div class="mb-3 font-size-lg">
              <p class="mb-0">You will no longer receive emails from us</p>
            </div>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import Unsubscribe from "@/js/mailsend/unsubscribe";

  export default {
    name: "Unsubscribe",
    components: {},
    data() {
      return {
        isUnsubscribed: true,
        message: "",
        show_message: false,
        unsubscribe_data: "",
        options: [],
        domain_id: "",
        unsubReason: "",
        user_id: "",
        contact_id: "",
        contact_data: {},
        unsubscribe_status: "",
        return_url: false,
        form: { reason: "" },
        newsletter_stat_id: "",
        newsletter_id: "",
        unsubscribe_form: { company_name: "", logo_url: null, redirect_url: "" },
        clicked: false,
        query: "",
      };
    },

    mounted: async function() {
      // Get Domain Id and User Id
      this.domain_id = window.domain;
      this.contact_id = this.$route.params.contact_id;
      this.newsletter_id = this.$route.params.newsletter_id;
      this.newsletter_stat_id = this.$route.params.newsletter_stat_id;
      this.query = this.$route.query.t;
      // Edit unsubscribe status on load
      await this.saveUnsubscribeStatus();

      // Get values
      this.unsubscribe_data = await Unsubscribe.getUnsubscribe(this.domain_id);
      this.unsubscribe_form.company_name = this.unsubscribe_data[0].company_name;
      this.unsubscribe_form.logo_url = this.unsubscribe_data[0].logo_url;
      this.unsubscribe_form.redirect_url = this.unsubscribe_data[0].redirect_url;
      this.unsubscribe_data[0].unsubscribe_reasons = JSON.parse(this.unsubscribe_data[0].unsubscribe_reasons);
      if (this.unsubscribe_data[0].unsubscribe_reasons.length >= 1) {
        this.unsubscribe_data[0].unsubscribe_reasons.map((item) => {
          this.options.push({ text: item.unsubscribe_reason, value: item.unsubscribe_reason });
        });
        this.options.push({ text: "Other (fill in reason below)", value: "Other" });
      }

      // Check unsubscribe status changed or not.
      await this.getContacts();
    },
    methods: {
      // Edit Unsubscribe status on load
      async saveUnsubscribeStatus() {
        // Call Python call to update the contacts table type, status columns
        try {
          let response = await Unsubscribe.editUnsubscribe(`${this.domain_id}/${this.contact_id}/${this.newsletter_id}/${this.newsletter_stat_id}?reason=unsubscribe&t=${this.query}`);
          console.log("Response", response);
        } catch (e) {
          console.log(e);
        }
      },

      // Get unsubscribe data
      async getContacts() {
        this.contact_data = await Unsubscribe.getContacts(`${this.domain_id}/${this.contact_id}/is-subscribed`);

        // Check unsubscribe status and unsubscribe reason
        if (this.contact_data.Status == true) {
          this.unsubscribe_status = "unSubscribed";
          if (this.unsubscribe_form.redirect_url) {
            setTimeout(() => {
              window.location.href = this.unsubscribe_form.redirect_url;
            }, 1000);
          }
        } else this.unsubscribe_status = "unSubscribedReason";
      },

      // Save unsubscription reasons json
      async save() {
        this.clicked = true;
        // Add new column in contacts table and store the unsubscription reason.
        if (this.form.reason == "Other") this.form.reason = this.unsubReason;

        try {
          await Unsubscribe.editUnsubscribeReason(`/${this.domain_id}/${this.contact_id}`, this.form, this.contact_id);
          this.clicked = false;
          this.isUnsubscribed = false;
          if (this.unsubscribe_form.redirect_url) {
            setTimeout(() => {
              window.location.href = this.unsubscribe_form.redirect_url;
            }, 1000);
          }
        } catch (e) {
          this.show_message = true;
          this.message = "Something went wrong try again.";
          console.log(e);
          this.clicked = false;
        }
        setTimeout(() => {
          this.message = "";
          this.show_message = false;
        }, 5000);
      },
    },
  };
</script>
