import Data from "@/js/core/data";

const Unsubscribe_Server = "https://api.${region}.500apps.com/mailsend/v1/unsubscribe/";
const Unsubscribe_Reason_Server = "https://api.${region}.500apps.com/mailsend/v1/unsubscribe/reason";
const Contacts_Server = "https://api.${region}.500apps.com/mailsend/v1/contacts/";

class Unsubscribe extends Data {
  // Get unsubscribe data
  getUnsubscribe(domain_id) {
    let url = Unsubscribe_Server + `${domain_id}`;
    let data = new Data(url);
    return data.get();
  }

  // Update email stats and contacts table
  editUnsubscribe(params) {
    let url = Unsubscribe_Server + params;
    let data = new Data(url);
    return data.get();
  }

  // Get contacts data by id
  getContacts(params) {
    let url = Contacts_Server + params;
    let data = new Data(url);
    return data.get();
  }

  // Update reason in contacts
  editUnsubscribeReason(params, form, id) {
    let url = Unsubscribe_Reason_Server + params;
    let data = new Data(url);
    data.disablePCors();
    return data.edit(id, form);
  }
}

export default new Unsubscribe();
