<template>
  <b-container>
    <b-row>
      <b-col md="7" class="mx-auto">
        <div class="d-flex align-items-center py-4">
          <b-img :src="unsubscribe_form.logo_url" alt="logo" height="40" v-if="unsubscribe_form.logo_url" />
          <h2 class="mb-0 ml-2 brand-heading">{{ unsubscribe_form.company_name }}</h2>
        </div>
        <b-card body-class="p-45">
          <h1 class="mb-2">Unsubscribe Successful</h1>
          <div class="mb-3 font-size-lg">
            <p class="mb-0">You will no longer receive emails from us</p>
          </div>
          <div class="mt-4">
            <h3 class="mb-3 pb-1">Please let us know why you unsubscribed?</h3>
            <b-form-textarea type="text" v-if="!(options.length >= 1)" class="mb-4 mt-3" required placeholder="Enter Unsubscribe reason" v-model="reason" />
            <b-form class="radio-group-mb-4">
              <b-form-radio-group stacked v-model="form.unsubscribe_reason" :options="options" />
            </b-form>
            <b-form-textarea type="text" class="mb-4 mt-3" v-if="form.unsubscribe_reason == 'Other'" required placeholder="Enter Unsubscribe reason" v-model="reason" />
            <div class="d-flex justify-content-between align-items-center">
              <div v-if="unsubscribe_form.redirect_url">
                <b-link :href="unsubscribe_form.redirect_url" target="_blank" disabled="true">Home</b-link>
              </div>
              <b-button type="submit" variant="primary" class="px-4 ml-auto" disabled="true">
                Submit
              </b-button>
            </div>
          </div>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  import Unsubscribe from "@/js/mailsend/unsubscribe";

  export default {
    name: "Unsubscribe",
    components: {},
    data() {
      return {
        form: {
          unsubscribe_reason: "",
        },
        reason: "",
        unsubscribe_data: "",
        options: [],
        unsubscribe_form: { company_name: "", logo_url: null, redirect_url: "" },
      };
    },

    mounted: async function() {
      // Get Domain Id and User Id
      this.domain_id = window.domain;
      // Get values
      this.unsubscribe_data = await Unsubscribe.getUnsubscribe(this.domain_id);
      this.unsubscribe_form.company_name = this.unsubscribe_data[0].company_name;
      this.unsubscribe_form.logo_url = this.unsubscribe_data[0].logo_url;
      this.unsubscribe_form.redirect_url = this.unsubscribe_data[0].redirect_url;
      this.unsubscribe_data[0].unsubscribe_reasons = JSON.parse(this.unsubscribe_data[0].unsubscribe_reasons);
      if (this.unsubscribe_data[0].unsubscribe_reasons.length >= 1) {
        this.unsubscribe_data[0].unsubscribe_reasons.map((item) => {
          this.options.push({ text: item.unsubscribe_reason, value: item.unsubscribe_reason });
        });
        this.options.push({ text: "Other (fill in reason below)", value: "Other" });
      }
    },
    methods: {},
  };
</script>
